/* ************************
   02.1: Common Classes
   ********************* */
/* Transitions */
.trans2 {
    @include transition(.2s, ease-in-out);
}

.trans3 {
    @include transition(.3s, ease-in-out);
}

.trans4 {
    @include transition(.4s, ease-in-out);
}

.trans5 {
    @include transition(.5s, ease-in-out);
}

.trans7 {
    @include transition(.5s, ease-in-out);
}

/* Font Family */
h1,
h2,
h3,
h4,
h5,
h6,
.poppins {
    font-family: $poppins;
}

body,
.rubik {
    font-family: $rubik;
}

/* Text White */
.text-white {
    *,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $white;
    }
} 

/* Border Radius */
.radius-5 {
    border-radius: 5px;
}

.radius-10 {
    border-radius: 10px;
}

.radius-50 {
    border-radius: 50px;
}

.radius {
    border-radius: 50%;
}

.regular {
    font-weight: $regular;
}

.medium {
    font-weight: $medium;
}

.semi-bold {
    font-weight: $semi-bold;
}

.bold {
    font-weight: $bold;
}

.extra-bold {
    font-weight: $extra-bold;
}

/* Box Shadow */
.box-shadow {
    box-shadow: 10px 20px 45px rgba(0, 0, 0, 0.07);
}

.box-shadow2 {
    box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.1);
}

.no-shadow {
    box-shadow: none !important;
}

.bg-img {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    &.blog {
        background-position: center top;
        background-size: contain;
    }
}

/* List Style */
.list-unstyled {
    padding: 0;
    margin: 0;
    list-style-type: none;
}

.list-inline {
    @extend .list-unstyled;
    li {
        display: inline-block;
    }
}

